import { mapState } from 'vuex'
export default{
	components:{
		
	},
	data(){
		return {
			nickName:'',
			avatarImg:''
		}
	},
	computed: {
		...mapState(['userInfo'])
	},
	onLoad(){
		this.nickName = this.userInfo.nickName
		this.avatarImg = this.userInfo.avatarImg
	},
	methods:{
		// 更换头像
		avatarImgChange(){
			uni.chooseImage({
			    count: 1, 
			    success: res => {
					this.avatarImg = res.tempFilePaths[0]
			    }
			});
		},
		//保存
		confirm(){
			if(this.avatarImg){
				if(this.nickName){
					this.userInfo.avatarImg = this.avatarImg
					this.userInfo.nickName = this.nickName
					this.$store.commit('setUserInfo',this.userInfo)
					uni.showToast({
						title:'保存成功',
						success:() => {
							setTimeout(() => {
								uni.navigateBack()
							},1000)
						}
					})
				}else{
					uni.showToast({
						icon:'none',
						title:'请输入昵称'
					})
				}
			}else{
				uni.showToast({
					icon:'none',
					title:'请上传头像'
				})
			}
		},
		IDAuth(){
			uni.navigateTo({
				url:'/sub_adviser/pages/personalInfo/IDAuth'
			})
		}
	}
}
